body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.header {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  padding: 50px 100px;
  height: 120px;
  background: #131313; }

.name-title {
  margin-right: auto;
  font-family: 'Raleway', sans-serif;
  color: #fff;
  font-weight: 300; }

.nav-container {
  display: flex;
  justify-content: center;
  flex-direction: row; }

.nav-box {
  padding-top: 20px;
  margin: 10px;
  display: flex;
  text-align: left;
  font-size: 11pt;
  color: #fff;
  font-family: 'Graphik', sans-serif;
  line-height: 1.25em; }

.nav-link {
  margin: 0 10px;
  text-decoration: none;
  color: #fff;
  height: 27px; }
  .nav-link:hover {
    border-bottom: 1px solid #00CEFF; }

.typeWriter {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  text-align: left;
  height: calc(100vh - 160px);
  background: #131313;
  font-color: #fff;
  padding: 0 100px; }

.arrow-wrapper {
  height: 200px; }

.Typist {
  height: 200px;
  font-family: 'Raleway';
  color: #fff;
  font-size: 20pt; }

.arrow-down {
  height: 35px;
  margin-right: 25px;
  -webkit-animation: show 15.5s, glow 4s infinite;
          animation: show 15.5s, glow 4s infinite;
  opacity: 1;
  -webkit-animation-delay: 0s, 15.5s;
          animation-delay: 0s, 15.5s; }

@-webkit-keyframes show {
  0% {
    opacity: 0; }
  50% {
    opacity: 0; }
  95% {
    opacity: .75; }
  100% {
    opacity: 1; } }

@keyframes show {
  0% {
    opacity: 0; }
  50% {
    opacity: 0; }
  95% {
    opacity: .75; }
  100% {
    opacity: 1; } }

@-webkit-keyframes glow {
  0% {
    opacity: .50; }
  50% {
    opacity: .75; }
  75% {
    opacity: 1; }
  100% {
    opacity: 1; } }

@keyframes glow {
  0% {
    opacity: .50; }
  50% {
    opacity: .75; }
  75% {
    opacity: 1; }
  100% {
    opacity: 1; } }

.box-one {
